.content-wrapper {
    width: 90%;
    display: flex;
    padding-top: 40px;
}

.content {
    width: 100%;
    min-height: calc(100vh - 20px);
    flex-grow: 1;
    margin: 0 60px;
}

.content section {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

@media (max-width: 480px) {
    .content-wrapper {
        width: 100vw;
        display: flex;
        flex-direction: column;
    }

    .content {
        width: 105%;
        height: 100%;
    }    
}